// }

.map-holder {
	width: 130px;
	height: 170px;
	margin: 0 20px;
	position: relative;
}
.map-shape {
	fill: #fff;
	stroke: rgb(150, 150, 150);
}
.map-img {
	position: absolute;
	display: none;
	.scr-mode & {
		display: block;
	}
}
.circle-mu {
	position: absolute;
	width: 13px;
	height: 13px;
	border-radius: 100%;
	border: 1px solid #424242;
	background: rgb(181, 181, 181);
	background: linear-gradient(
		135deg,
		rgba(181, 181, 181, 1) 0%,
		rgba(247, 247, 247, 1) 100%
	);
	&:hover {
		background: rgb(213, 213, 213);
		background: linear-gradient(
			135deg,
			rgba(213, 213, 213, 1) 0%,
			rgba(249, 249, 249, 1) 100%
		);
	}
	&.active {
		background: rgb(187, 209, 226);
		background: linear-gradient(
			135deg,
			rgba(187, 209, 226, 1) 0%,
			rgba(50, 93, 123, 1) 100%
		);
	}
}
.map-svg {
	.scr-mode & {
		display: none;
	}
}
.default_grad {
	fill: url(#default_grad);
	stroke: rgb(60, 60, 60);
	stroke-miterlimit: 10;
}
.active_grad {
	fill: url(#active_grad);
	stroke: rgb(60, 60, 60);
	stroke-miterlimit: 10;
}
.hover_grad {
	fill: url(#hover_grad);
	stroke: rgb(60, 60, 60);
	stroke-miterlimit: 10;
}
circle:hover {
	cursor: pointer;
}
