
















































































































.scroll {
	height: 100vh;
	// overflow-y: auto;
	overflow-x: hidden;
	background: linear-gradient(0deg, #bbbdde 0%, white 100%);
}

.news-title {
	font-weight: 500;
	font-size: x-large;
}

.v-list-item::after {
	content: none !important;
}

@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
	/* IE10+ CSS styles go here */
	.theme--light.v-expansion-panels .v-expansion-panel {
		top: 50px;
	}
}

.v-expansion-panels:not(.v-expansion-panels--accordion):not(.v-expansion-panels--tile)
	> .v-expansion-panel--next-active
	.v-expansion-panel-header {
	border-bottom-left-radius: 0px !important;
	border-bottom-right-radius: 0px !important;
}

.theme--light.v-expansion-panels.v-expansion-panels--focusable
	.v-expansion-panel-header--active:hover::before,
.theme--light.v-expansion-panels.v-expansion-panels--focusable
	.v-expansion-panel-header--active::before {
	opacity: 0 !important;
}

.theme--light.v-expansion-panels.v-expansion-panels--focusable
	.v-expansion-panel-header:hover::before {
	opacity: 0 !important;
}

.v-expansion-panel-header--active {
	color: white;
	background-color: #9d1f30;
}

.v-responsive {
	max-width: 20%;
}

.page-title {
	flex: 1;
	color: #9d1f30;
}
.v-expansion-panels {
	width: 65%;
	top: 50px;
	margin-bottom: 200px;
}
.v-expansion-panel {
	margin-left: 0%;
}
.content {
	padding-top: 20px;
	flex-direction: row !important;
}

.v-list-item {
	flex-direction: row;
}

.v-expansion-panel-content__wrap {
	display: flex;
	flex-direction: row !important;
}

.v-card__text {
	padding-left: 0;
	padding-top: 0;
	line-height: 170%;
}

.v-card__actions {
	padding-top: 0;
}

.v-list-item__content {
	padding-bottom: 0;
}

@media only screen and (max-width: 768px) {
	.v-responsive {
		max-width: 100%;
	}
	.v-list-item {
		flex-direction: column-reverse;
	}
	.news-title {
		font-weight: 500;
		font-size: large;
	}

	.v-expansion-panels {
		width: 85%;
		top: 15px;
	}

	.v-list-item {
		padding: 0px;
	}
}
