

















































































.active-btn {
	&,
	.v-icon {
		color: #9d1f30 !important;
	}
}
#AnimationPlayerToolbar {
	position: absolute;
	z-index: 1;
	bottom: 0;
	left: 0;
	right: 0;
	background-color: #ff000000;
	background-image: linear-gradient(180deg, #ff000000 rgba(255, 0, 0, 0) 00);
	border-top: 1px solid #d0d0d000 !important;
	.v-toolbar__content {
		padding: 0;
	}
	.v-btn {
		&.v-btn--icon {
			color: rgba(0, 0, 0, 1);
		}
		.v-icon {
			font-size: 28px;
		}
	}
}
