#iron3d .regulation1.icon:before {
	top: 0;
	left: 0;
}

#iron3d .navigation.play.button {
	&:after {
		display: inline-block;
		font: normal normal normal 24px/1 'Material Design Icons';
		font-size: inherit;
		text-rendering: auto;
		line-height: inherit;
		-webkit-font-smoothing: antialiased;
		content: '\F040A';
		font-size: 2.6em;
	}
	&:hover:after {
		color: #9d1f30;
	}
}

.fill-width {
	width: 100%;
}

.animationcapture {
	position: absolute;
}
.animation-section {
	max-width: 100%;
	background: rgb(187, 189, 222);
	background: linear-gradient(
		0deg,
		rgba(187, 189, 222, 1) 0%,
		rgba(255, 255, 255, 1) 100%
	);
}
.iron3d-controls #top {
	display: none;
}
.blink_me {
	z-index: 999999;
	position: absolute;
	right: 15px;
	top: 15px;
	font-size: 16px;
	animation: blinker 2s linear infinite;
}
#loading-wrapper {
	z-index: 999999;
}

@keyframes blinker {
	50% {
		opacity: 0;
	}
}

#canvas-id {
	z-index: -1;
	// pointer-events: none;
}

#img-id {
	z-index: -1;
}

#animID {
	overflow: hidden;
}

