

























































































































































































#elements-container {
	display: flex;
	position: absolute;
	max-width: 440px;
	top: 150px;
	left: 15px;
	right: 15px;
	margin-left: auto;
	margin-right: auto;
	// left: 50%;
	// transform: translate(-50%, -0%);

	background: rgba(0, 0, 0, 0);
	z-index: 1;
}

.element-box {
	align-self: flex-start;
	width: 2.5rem;
}

.element-col {
	flex-grow: 0;
}

.element-thumb {
	box-shadow: 3px 3px 5px #8888884f;
	// border: 1px solid #5252523d;
	background-color: rgb(255, 255, 255);
	&.active {
		background-color: #ffffff;
		border: 1px solid #9d1f30;
	}
}
