#subtitles {
	position: absolute;
	z-index: 4;
	bottom: 50px;
	left: 0;
	width: 100%;
	.video-js {
		position: absolute;
		bottom: 300%;
		// width: 700px;
		// height: 500px;
		margin-bottom: 100px;
		left: -1000px;
	}
	.sectionstep-cc {
		position: absolute;
		z-index: 50;
		// display: flex;
		bottom: 0;
		width: 100%;
		padding: 15px 20px 15px;
		text-align: center;
		color: #ffffff;
		&.cc-style-subtitle {
			.cc-cue {
				display: inline;
				padding: 3px 5px;
				background-color: rgba(0, 0, 0, 0.7);
			}
		}
	}

	.cc-style-text {
		overflow-y: auto;
		max-height: 67px;
		display: flex;
		//margin-bottom:5px;
		background-color: rgba(0, 0, 0, 0.6);
		.active-cue {
			background-color: rgba(0, 0, 0, 0.8);
		}
	}

	.cc-style-text-m {
		overflow-y: auto;
		max-height: 75px;
		// width: 80%;
		//margin-bottom:5px;
		background-color: rgba(0, 0, 0, 0.6);
		#active-cue {
			background-color: rgba(0, 0, 0, 0.8);
		}
	}

	.cc-span {
		padding-left: 2.5px;
		padding-right: 2.5px;
	}
	.cc-span-m {
		padding-left: 2.5px;
		padding-right: 2.5px;
	}

	.full_subs_div {
		display: flex;
		position: absolute;
		// right: 30px;
		// bottom: 6%;
		right: 20px;
		position: fixed;
	}

	.full_subs {
		min-width: 0px !important;
		padding: 6px;
		background-color: #ffffff00;
		color: #ffffff;
	}

	.full_subs_div_m {
		display: flex;
		position: absolute;
		// right: 30px;
		// bottom: 6%;
		right: 0px;
		bottom: 70px;
		position: fixed;
	}

	.full_subs_m {
		min-width: 0px !important;
		padding: 6px;
		background-color: #ffffff00;
		color: #ffffff;
	}

	.cc-cue-full {
		width: 95%;
	}
}

.card-text- {
	padding-top: 20px !important;
}

.card-text-step {
	padding-left: 10px !important;
	font-size: 0.9rem;
	font-weight: 300;
}

.v-dialog .v-dialog--active {
	overflow-x: hidden;
}
