




































































































































































































































































































































































































































// required and to get updates
@import './node_modules/vue-cookieconsent-component/src/scss/cookie-consent';
// example or use it
@import './node_modules/vue-cookieconsent-component/src/scss/cookie-consent-bottom';
@import './node_modules/vue-cookieconsent-component/src/scss/cookie-consent-transition';

.left-column-wraper-full {
	max-width: 500px;
	left: 5vw;
	margin-right: 5vw;
	position: absolute;
	z-index: 1;
	justify-content: center;
	top: 7%;
}

.left-column-wraper-half {
	max-width: 500px;
	left: 5vw;
	margin-right: 5vw;
	position: absolute;
	z-index: 1;
	justify-content: center;
	top: 7%;
}

a {
	text-decoration: none;
}

.text-full {
	height: 100%;
}

.text-half {
	height: 95%;
}

.container {
	padding: 0%;
}

.theme--light.v-list {
	background-color: rgb(1, 1, 1, 0);
}

.theme--light.v-card {
	background-color: rgb(1, 1, 1, 0);
	border: dotted thin rgba(0, 0, 0, 0.4) !important;
}

.cc {
	position: absolute;
}

.resnews {
	padding-top: 50px;
}

.headline {
	font-weight: 700 !important;
	text-transform: uppercase;
	word-break: break-word;
}

.install-app-btn {
	border: dotted thin rgba(0, 0, 0, 0.4) !important;
	border-color: rgb(255, 0, 0) !important;
	margin-right: 10px;
	margin-bottom: 10px;
}
.start-app-btn {
	margin-right: 10px;
	margin-bottom: 10px;
}

.bg {
	width: 100%;
	height: 850px;
	margin-left: 10%;
	background: url('../assets/homebackground24.jpg') no-repeat center;
	background-size: contain;
	transform: scale(1);
	opacity: 0.4;
	position: fixed;
	top: 40px;
	pointer-events: none;
}

.v-card__text {
	color: rgb(0, 0, 0) !important;
}

.v-content {
	background-color: #bbbdde;
}

.v-card {
	background-color: #00000000;
}

.cookie-consent {
	bottom: 0% !important;
	right: 0% !important;
	width: 30% !important;
	left: 70% !important;

	margin-left: -20px;
	margin-bottom: 20px;

	padding: 1.3em;
	text-align: justify;

	background: #9d1f30 !important;
	color: white;

	border-radius: 5px;
}

.btn-info{
	padding-left: 50px;
	padding-right: 50px;
	padding-top: 5px;
	padding-bottom: 5px;
	background: #630e1a;
	border-radius: 5px;
	margin-top: 10px;
}

@media only screen and (max-width: 1400px) {
	.bg {
		width: 100%;
		height: 850px;
		margin-left: 20vw;
		background: url('../assets/homebackground24.jpg') no-repeat center
			center;
		background-size: cover;
		transform: scale(1);
		opacity: 0.4;
	}
}

@media only screen and (max-width: 1263px) {
	.left-column-wraper-half {
		max-width: 500px;
		left: 5vw;
		margin-right: 5vw;
		position: absolute;
		z-index: 1;
		// justify-content: center;
		justify-content: space-between;
		top: 7%;
		height: 92%;
	}

	.home-text-half {
		overflow: hidden;
		text-overflow: ellipsis;
		display: -webkit-box;
		-webkit-box-orient: vertical;
		-webkit-line-clamp: 6; /* number of lines to show */
		line-height: 1.5; /* fallback */
		max-height: 6 * 1.5em; /* fallback */
	}
}

@media only screen and (max-width: 600px) {
	.cookie-consent {
		bottom: 0% !important;
		right: 0% !important;
		width: 100% !important;
		left: 0% !important;

		margin-left: 0px;
		margin-bottom: 0px;

		padding: 2em;
		text-align: justify;
	}

	.bg {
		width: 100%;
		height: 850px;
		margin-left: 20vw;
		background: url('../assets/homebackground24.jpg') no-repeat center
			center;
		background-size: cover;
		transform: scale(1);
		opacity: 0.1;
	}

	.v-card {
		background-color: #00000000 !important;
	}
}

.online-disabled {
	cursor: default;
}

.home-text-half {
	overflow: hidden;
	text-overflow: ellipsis;
	display: -webkit-box;
	-webkit-box-orient: vertical;
	-webkit-line-clamp: 6; /* number of lines to show */
	line-height: 1.6; /* fallback */
	max-height: 7 * 1.5em; /* fallback */
}

.home-text-full {
	padding-bottom: 0px;
}

.v-card__actions {
	padding-left: 15px;
}

.t {
	display: flex;
	flex-direction: column;
}

.v-icon.v-icon::after {
	content: none !important;
}

.btns {
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
}
