	&,
	.v-icon {
		color: #9d1f30 !important;
	}
}
#AnimationPlayerToolbar {
	position: absolute;
	z-index: 1;
	bottom: 0;
	left: 0;
	right: 0;
	background-color: #e0e0e0;
	background-image: linear-gradient(180deg, #f0f0f0, #d0d0d0);
	border-top: 1px solid #d0d0d0 !important;
	.v-toolbar__content {
		padding: 0;
	}
	.v-btn {
		&.v-btn--icon {
			color: rgba(0, 0, 0, 1);
		}
		.v-icon {
			font-size: 28px;
		}
	}
	.section-steps {
		position: absolute;
		z-index: 2;
		top: 0px;
		width: 100%;
		height: 0px;
		//background-color: rgba(0, 0, 0, 0.2);
		.steps-wrapper {
			width: 100%;
			.step-block {
				position: relative;
				.step-marker {
					width: 10px;
					cursor: pointer;
					position: absolute;
					bottom: 0;
					height: 10px;
					left: 50%;
					transform: translateX(-50%);
					//background-color: #e0e0e0;
					&:after {
						content: ' ';
						display: block;
						position: absolute;
						width: 2px;
						height: 100%;
						background-color: white;
						left: 4px;
					}
					&.done-step {
						&:after {
							background-color: #ff9800;
						}
					}
					.marker-icon {
						position: absolute;
						top: -20px;
						left: -7px;
						transform: rotate(90deg);
						display: none;
					}
					&.current-step {
						.marker-icon {
							display: flex;
						}
					}
					&.current-step:after,
					&:hover {
						background-color: #ff9800 !important;
						color: #ff9800 !important;
					}
				}
			}
		}
	}
	.progress-bar {
		position: absolute;
		top: -10px;
		left: 0;
		width: 100%;
		z-index: 1;
		height: 10px;
		.v-slider {
			margin: 0;
		}
		.v-input__slot {
			margin-bottom: 0;
		}
		.v-slider__track-container {
			height: 10px;
			:first-child {
				background-color: #000000 !important;
				opacity: 0.5;
			}
			:nth-child(2) {
				background-color: #9d1f30 !important;
				opacity: 1;
			}
		}
		.v-slider__thumb {
			display: none;
		}
		&:hover {
			.v-slider__thumb {
				display: block;
			}
		}
	}
}

@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
