			//background-color: rgba(0, 0, 0, 0.2);
			.steps-wrapper {
				width: 100%;
				.step-block {
					position: relative;
					.step-marker {
						width: 10px;
						cursor: pointer;
						position: absolute;
						bottom: 0;
						height: 10px;
						left: 50%;
						transform: translateX(-50%);
						//background-color: #e0e0e0;
						&:after {
							content: ' ';
							display: block;
							position: absolute;
							width: 2px;
							height: 100%;
							background-color: white;
							left: 4px;
						}
						&.done-step {
