						}
					}
				}
			}
		}

		.progress-bar {
			position: absolute;
			top: -10px;
			left: 0;
			width: 100%;
			z-index: 1;
			height: 10px;
			.v-slider {
				margin: 0;
				min-height: 10px !important;
				background-color: #9d1f30;
			}
			.v-input__slot {
				margin-bottom: 0;
			}
			.v-slider__track-container {
				height: 10px;
				:first-child {
					background-color: #7e7e7e !important;
					opacity: 1;
				}
				:nth-child(2) {
					background-color: #9d1f30 !important;
					opacity: 1;
				}
			}
			.v-slider__thumb {
				display: none;
			}
			&:hover {
				.v-slider__thumb {
					display: block;
				}
			}
		}
	}
}
@media only screen and (max-width: 350px) {
	.v-toolbar__content .v-btn.v-btn--icon.v-size--default,
	.v-toolbar__extension .v-btn.v-btn--icon.v-size--default {
		width: 40px !important;
	}
}
